.card {
    background-color: black;
    color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 250px; 
    height: 370px !important; 
    display: flex; 
    align-items: center; 
    flex-direction: column;
    margin: 25px;
    box-shadow: 5px 5px 30px 0px black;
    transition: transform 0.5s; 
}
  .card:hover {
    cursor: pointer;
    transform: translate(0px, 7.5px); 
    box-shadow: 5px 5px 30px 0px black; 
  }

  .card h3 {
    color: red;
    margin-bottom: 10px;
  }
  
  .card p {
    margin: 0;
  }
  .card h4{
    color: red;
    margin: 5px;
  }
  
  #image {
    width: 150px;
    height: 120px;
    background-color: white;
    border: solid red;
    flex-shrink: 0; 

  }

